body{
    padding:0;
    margin:0;
}
#ace_editor{
	position:relative;
	top:0;
	left:0;
	width:100% !important;
	height:100% !important;
}
.split{
	display:-webkit-flex;
	display:flex;
	-webkit-flex-direction:row;
	        flex-direction:row;
	position:fixed;top:0;left:0;width:100%;
	height:100%;
}
.splitItem{
	-webkit-flex: 1 1;
	        flex: 1 1;
	-webkit-transition: 0.1s opacity;
	transition: 0.1s opacity;
}
.scroll{
	overflow:scroll;
}

.error {
	background-color:#ffdddd !important;
}

.loading{
    opacity: 0.5;
	pointer-events: none;
}
